.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}
.wrapper {
    padding-bottom: 50px;
    background: /*#ECEFF1*/rgb(175, 175, 175);
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
  
  
.title {
    width: 100%;
    text-align: center;
    font-size: 2rem;
    color: #2A77AE;
    padding-left: 3%;
    padding-right: 3%;
    margin-bottom: 50px;
  }
  

.card {
  border: 4px rgb(99, 99, 99) solid;
  border-radius: 2px;
  width: 150px;
  height: 150px;
  margin: 1rem;
  position: relative;
  box-shadow: 4px 4px 10px rgb(56, 56, 56);
  transition: all .2s ease-in-out;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  background:rgb(10, 10, 10);
}

.card > .img-container > img {
  height: 100%;
}

.card:hover {
  box-shadow: 8px 8px 20px rgb(56, 56, 56);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.card:active {
  -webkit-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}




